.form {
  display: flex;
  flex-direction: column;

  margin-bottom: 30px;
  padding: 10px;

  width: 400px;

  outline: 2px solid silver;
}

.label {
  display: flex;
  flex-direction: column;

  margin-bottom: 25px;

  font-weight: 600;
}

.input {
  margin-top: 6px;

  outline: none;
  border: 1px solid silver;
}

.input:focus {
  outline: 2px solid rgb(71, 172, 255);
}

.button {
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  padding: 10px 30px;
  margin: 10px 20px;
  position: relative;
  overflow: hidden;
  border: 2px solid #5c99f5;
  border-radius: 8px;
  font-family: 'Montserrat', sans-serif;
  color: #5c99f5;
  transition: 0.2s ease-in-out;
}
.button:before {
  content: '';
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.5)
  );
  height: 50px;
  width: 50px;
  position: absolute;
  top: -8px;
  left: -75px;
  transform: skewX(-45deg);
}
.button:hover {
  background: #5c99f5;
  color: #fff;
}
.button:hover:before {
  left: 150px;
  transition: 0.5s ease-in-out;
}
